*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
#root,
.app {
  height: 100%;
  width: 100%;
  font-size: 0.875rem;
  line-height: 1.25rem;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity, 1));
  --tw-bg-opacity: 1;
  background-color: rgb(27 28 34 / var(--tw-bg-opacity, 1));
}

:root {
  --radius: 8px;
}

input[type="color"] {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background: none;
  border: 0;
  cursor: pointer;
  padding: 0;
}

.bg-sidebar{
  --tw-bg-opacity: 1;
  background-color: rgb(27 28 34 / var(--tw-bg-opacity, 1));
}

/* Filepond */
.filepond--panel-root{
  --tw-bg-opacity: 1 !important;
  background-color: rgb(27 28 34 / var(--tw-bg-opacity, 1)) !important;
}

.filepond--drop-label.filepond--drop-label label{
  cursor: pointer;
}

.filepond--drop-label{
  --tw-text-opacity: 1 !important;
  color: rgb(210 210 210 / var(--tw-text-opacity, 1)) !important;
  cursor: pointer;
}

.filepond--drop-label:hover{
  --tw-bg-opacity: 1 !important;
  background-color: rgb(61 61 61 / var(--tw-bg-opacity, 1)) !important;
  --tw-text-opacity: 1 !important;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1)) !important;
}

/* Component Classes */

/* Search Page */
.search{
  margin-left: auto;
  margin-right: auto;
  display: flex;
  height: 100%;
  width: 75%;
  flex-direction: column;
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
}

.search__title{
  margin-top: 3.5rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 400;
}

.search__subtitle{
  margin-bottom: 0.75rem;
  --tw-text-opacity: 1;
  color: rgb(107 114 128 / var(--tw-text-opacity, 1));
}

.search__content{
  display: flex;
  width: 100%;
  flex-direction: column-reverse;
  gap: 2rem;
  padding-bottom: 2rem;
  padding-top: 0.5rem;
}

@media (min-width: 768px){

  .search__content{
    flex-direction: row;
  }
}

.search__courses-grid{
  display: grid;
  flex-basis: 60%;
  grid-auto-rows: minmax(0, 1fr);
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 1.5rem;
}

@media (min-width: 1280px){

  .search__courses-grid{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

.search__selected-course{
  height: fit-content;
  min-width: 350px;
  flex-basis: 40%;
  overflow: hidden;
  border-radius: var(--radius);
  border-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(152 152 253 / var(--tw-border-opacity, 1));
  --tw-bg-opacity: 1;
  background-color: rgb(37 38 47 / var(--tw-bg-opacity, 1));
}

/* Selected Course Component */
.selected-course{
  overflow: hidden;
  padding-top: 2.25rem;
  padding-bottom: 2.25rem;
  padding-left: 2.25rem;
  padding-right: 2.25rem;
}

.selected-course__title{
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(210 210 210 / var(--tw-text-opacity, 1));
}

.selected-course__author{
  padding-top: 0.75rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  --tw-text-opacity: 1;
  color: rgb(156 163 175 / var(--tw-text-opacity, 1));
}

.selected-course__enrollment-count{
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity, 1));
}

.selected-course__content{
  margin-top: 1.25rem;
}

.selected-course__description{
  margin-bottom: 1rem;
  --tw-text-opacity: 1;
  color: rgb(107 114 128 / var(--tw-text-opacity, 1));
}

.selected-course__sections{
  margin-top: 1.5rem;
}

.selected-course__sections-title{
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: rgb(210 210 210 / 0.9);
}

.selected-course__footer{
  margin-top: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.selected-course__price{
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(179 179 253 / var(--tw-text-opacity, 1));
}

/* Checkout Pages */
.checkout{
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.checkout__content{
  margin-top: 2.5rem;
  display: flex;
  width: 100%;
  max-width: 1024px;
  flex-direction: column;
  align-items: center;
}

/* Completion Page */
.completion{
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
}

.completion__content{
  text-align: center;
}

.completion__icon{
  margin-bottom: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgb(34 197 94 / var(--tw-bg-opacity, 1));
  padding: 0.75rem;
}

.completion__title{
  margin-bottom: 0.75rem;
  font-size: 2.25rem;
  line-height: 2.5rem;
  font-weight: 700;
}

.completion__message{
  margin-bottom: 0.25rem;
}

.completion__action{
  margin-top: 0.5rem;
  display: flex;
  cursor: pointer;
  justify-content: center;
  border-radius: var(--radius);
  --tw-bg-opacity: 1;
  background-color: rgb(68 197 178 / var(--tw-bg-opacity, 1));
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.completion__action:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(112 211 196 / var(--tw-bg-opacity, 1));
}

/* Landing Page */
.landing{
  width: 75%;
}

.landing__hero{
  margin-top: 3rem;
  display: flex;
  height: 500px;
  align-items: center;
  justify-content: space-between;
  border-radius: var(--radius);
  --tw-bg-opacity: 1;
  background-color: rgb(37 38 47 / var(--tw-bg-opacity, 1));
}

.landing__hero-content{
  margin-left: auto;
  margin-right: auto;
  flex-basis: 50%;
  padding-left: 4rem;
  padding-right: 4rem;
}

.landing__title{
  margin-bottom: 1rem;
  font-size: 2.25rem;
  line-height: 2.5rem;
  font-weight: 700;
}

.landing__description{
  margin-bottom: 2rem;
  font-size: 1.125rem;
  line-height: 1.75rem;
  --tw-text-opacity: 1;
  color: rgb(156 163 175 / var(--tw-text-opacity, 1));
}

.landing__cta{
  width: fit-content;
}

.landing__cta-button{
  border-radius: calc(var(--radius) - 2px);
  --tw-bg-opacity: 1;
  background-color: rgb(120 120 252 / var(--tw-bg-opacity, 1));
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.landing__cta-button:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(152 152 253 / var(--tw-bg-opacity, 1));
}

.landing__hero-images{
  position: relative;
  height: 100%;
  flex-basis: 50%;
  overflow: hidden;
  border-top-right-radius: var(--radius);
  border-bottom-right-radius: var(--radius);
}

.landing__hero-image{
  object-fit: cover;
  opacity: 0;
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 500ms;
  animation-duration: 500ms;
}

.landing__hero-image--active{
  opacity: 1;
}

.landing__featured{
  margin-left: auto;
